import actionlist from "@/components/actionlist.vue";
export default {
	data() {
		return {
			tab:0,//行动，围观，收藏
			cardList:[],
			pageNum:1,//当前页数
			pageSize:10,//每页条数
			total:'',
		};
	},
	onLoad() {
		this.inDada();
	},
	methods: {
		goStep(){
			uni.navigateTo({
				url: `/pages/action/step1`
			});
		},
		inDada(){
			let token='';
			let id='';
			try{
				token=uni.getStorageSync('token');
				id=uni.getStorageSync('id');
			}catch(e){
				//TODO handle the exception
			}
			   this.xd_request_post(this.xdServerUrls.xd_pushByUserIdList,
			   {
			   token:token,
			   userId:id,
			   pageNum:1,
			   pageSize:10,
			   },
			   false
			   
			       ).then(res=>{
					   console.log(res)
						 this.cardList=res.obj.list;
						 this.pageNum=res.obj.nextPage;
						 this.total=res.obj.total;
			   	}).catch(Error=>{
			   		console.log(Error)
			   	})
			   
			    
			},
			getReachList(){
					
					if(this.pageNum==0){
						uni.showLoading(
						{
							title: '没有更多数据了'
						})
						setTimeout(function () {
						    uni.hideLoading();
						}, 1000);
						return false
					}
					uni.showLoading(
					{
						title: '加载中..',
						mask:true
					})
					this.xd_request_post(this.xdServerUrls.xd_pushByUserIdList,
					{
						token:uni.getStorageSync('token'),
						userId:uni.getStorageSync('id'),
						pageNum:this.pageNum,
						pageSize:this.pageSize,
					},
					false
						   ).then(res=>{
							   this.pageNum=res.obj.nextPage;
							console.log(res.obj.list);		
							this.cardList = this.cardList.concat(res.obj.list);					
							setTimeout(function () {
							    uni.hideLoading()
							}, 1000);
							// uni.hideNavigationBarLoading();//关闭加载动画
							
						})
				}
				
	},
	onReachBottom() {
		this.getReachList()
	},
	onPullDownRefresh() {
		
	},
	components:{
		actionlist
	}
};
